:root {
	--background: #1f1b24;
	--boxBackground: #ffe4c4;
}

body {
	background-color: var(--background);
	color: aliceblue;
	user-select: none;
	font-family: Roboto, Raleway, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	width: 100%;
}

.App {
	text-align: center;
}

#board {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 200px;
	width: 80%;
	margin: 48px auto;
	line-height: 108px;
	font-size: 80px;
	background-color: var(--boxBackground);
	border-radius: 8px;
	text-transform: uppercase;
	color: #1f1b24;
	font-weight: 800;
}

#inputbox {
	font-size: 20px;
	margin: 0 10px;
	padding: 0;
	height: 48px;
	box-sizing: border-box;
	text-transform: uppercase;
	border: none;
	outline: none;
	padding: 8px 16px;
}

#btnreset {
	background-color: var(--boxBackground);
	height: 48px;
	width: 72px;
    font-weight: 500;
	font-size: 20px;
	padding: 0px !important;
	margin: 0px;
	outline: none;
	border: none;
	cursor: pointer;
}

#timetaken {
	margin: 24px;
	font-size: 32px;
}